import React from 'react';
import './HighlightCards.css'

const HighlightCards = (props) => {
  return (
    <div>
        <div className="highlightContainer">
            <div className="highlightImgContainer">
                <img src={props.photo} alt="" />
            </div>
            <div className="topic">
                <h3>{props.text}</h3>
            </div>
            <div className="tags">
                <p>{props.tag}</p>
            </div>
            <div className="desc">
                <p>{props.desc}</p>
            </div>
        </div>
    </div>
  )
}

export default HighlightCards