import React from 'react';
import './Navbar.css';

const Navbar = (props) => {
  return (
    <div className='nav'>
        <ul>
            <li><img src={props.logo} alt="" /></li>
            <li className='home'>Home</li>
            <li className='about'><a href='#bottom'>About</a></li>
            <li className='contact'><a href='#bottom'>Contact me</a></li>
        </ul>
    </div>
  )
}

export default Navbar