import React from 'react';
import './ImgCards.css';
const ImgCards = (props) => {
  console.log("props received:", props);
  return (
    <div>
        <div className="imgCardsContainer">
          <div className="imgContainer">
            <img src={props.image} alt="" />
          </div>
          <div className="imgDesc">
            <div className="imgCardHeading">
              #Head1
            </div>
            <div className="desc">
              Lorem ipsum dolor sit amet, consectetur adipisicing elit. Veritatis consequuntur, ab, distinctio esseror.
            </div>
          </div>
        </div>
    </div>
  )
}

export default ImgCards